import React from "react";
import cssVar from "views/mobileView/styles";
import { AiFillRightCircle, AiOutlineRight } from 'react-icons/ai'
import MintboxIcon from "./MintboxIcon";
import AMCIcon from "./AMCIcon";
import { CurrencyFormat } from "./CurrencyFormat";
import { baseImgURL } from "assets/Constant";
export default function SliderComponent({ navigation, type, fundType, component: { header = "", description = "", content, right, rightAction = () => { } } }) {
    const renderRightSide = () => {
        switch (right) {
            case 'See all':
                return <span className="greenChipHeader">See all <AiOutlineRight size={8} /></span>
            case 'All':
                return <span className="greenChipHeader">All funds <AiOutlineRight size={8} /></span>
            case 'More':
                return <span className="greenChipHeader">More <AiOutlineRight size={8} /></span>
            default:
                return <></>;
        }
    };
    const box_card = (slide) => {
        let headerColor = ''
        let textColor = ''
        let backgroundColor = '';
        let buttonColor = '';
        let buttonTextColor = ''
        switch (slide.Theme) {
            case "DARKGREEN": {
                headerColor = cssVar.getPropertyValue('--white')
                textColor = cssVar.getPropertyValue('--subtle')
                backgroundColor = cssVar.getPropertyValue('--darkGreen')
                buttonColor = cssVar.getPropertyValue('--primaryGreen')
                buttonTextColor = cssVar.getPropertyValue('--darkGreen')
                break;
            }
            case "GREEN": {
                headerColor = cssVar.getPropertyValue('--black')
                textColor = cssVar.getPropertyValue('--grey')
                backgroundColor = cssVar.getPropertyValue('--lighter')
                buttonColor = cssVar.getPropertyValue('--white')
                buttonTextColor = cssVar.getPropertyValue('--secondaryGreen')
                break;
            }
            default: {
                headerColor = cssVar.getPropertyValue('--black')
                textColor = cssVar.getPropertyValue('--grey')
                backgroundColor = cssVar.getPropertyValue('--lighter')
                buttonColor = cssVar.getPropertyValue('--white')
                buttonTextColor = cssVar.getPropertyValue('--secondaryGreen')
            }
        }
        return (<div
            key={slide.UID}
            style={{
                marginRight: "4%",
                padding: "4% 4% 0 4%",
                border: "1px solid " + cssVar.getPropertyValue("--light"),
                borderRadius: '8px',
                backgroundColor: backgroundColor,
                display: "inline-flex",
                flexDirection: "column",
                width: content?.length === 1 ? "96%" : "85%",
                whiteSpace: "normal"
            }}
        >
            <div className='header1dark' style={{ marginBottom: "4%",color: headerColor }}>
                {slide.SubTitle}
            </div>
            <div className='bodyTextLight' style={{marginBottom: "4%",lineHeight:"21px", color: textColor, width: "85%"}}>
                {slide.Description}
            </div>
            <div style={{ display: "flex" }}>
                <div style={{ width: "70%", display: "flex", alignItems: "flex-end", marginBottom:'4%' }}>
                    <button className="solidCardButton"
                        style={{
                            borderColor: buttonTextColor,
                            border: '1px solid '+ buttonTextColor,
                            color: buttonTextColor,
                            backgroundColor: buttonColor,
                        }}
                        onClick={() => {
                            if(slide.onClick){
                                slide.onClick()
                            }
                            else if(slide.ActionTarget !== ''){
                                var a= JSON.parse(JSON.parse(slide.ActionTarget))
                                navigation.push(a)
                            }
                        }}
                    >
                        {slide.ActionText}
                    </button>
                </div>
                <div style={{ width: "30%", position: "relative", textAlign:'right' }}>
                    <img style={{ maxWidth: "100%", height: "80px", position:'absolute',right:0, bottom:0 }} src={slide.URL || baseImgURL+'SVG12.svg'} alt="" />
                </div>
            </div>
        </div>)
    }
    const recently_viewed = (slide) => {
        return (<div
            key={slide.ID}
            onClick={() => {
                if (slide.type === 1) {
                    navigation.push("/ETF-detail/" + slide.ID)
                } else if (slide.type === 2) {
                    navigation.push("/fund-detail/" + slide.ID)
                } else if (slide.type === 3) {
                    navigation.push("/mintbox-detail/" + slide.ID)
                }
            }}
            style={{
                marginRight: "4%",
                padding: "4%",
                border: "1px solid " + cssVar.getPropertyValue("--light"),
                borderRadius: '8px',
                backgroundColor: cssVar.getPropertyValue("--white"),
                display: "inline-flex",
                flexDirection: "column",
                width: content?.length === 1 ? "96%" : "80%",
                minHeight: slide.type===3?"160px":"140px",
                whiteSpace: "normal",
            }}
        >
            <div style={{display:"flex",justifyContent:'space-between',marginBottom:"4%"}}>
                {
                    slide.type === 3?
                    <MintboxIcon id={slide.ID} height='40px' width='40px'/>
                    :
                    <AMCIcon amcName={slide.AMCName} height='40px' width='40px' />
                }
                <div>
                    <div className="smallTextLight" style={{border:"1px solid "+cssVar.getPropertyValue('--light'),padding:"4px 8px",borderRadius:"4px",whiteSpace:"nowrap"}}>
                        <img src={baseImgURL+'coin.webp'} style={{width:"12px",height:"12px",marginRight:"6px"}} />{slide.type !==1? 'Min investment ':'Latest NAV ' }{CurrencyFormat(slide.MinInv,'long',2)}
                    </div>
                </div>
            </div>
            <div style={{ display: "flex",marginBottom:"3%",gap:'4%' }}>
                <div className="header2dark lineClamp" style={{ flex: '1'}}>
                    {slide.Name}
                </div>
                <div>
                    <div className="smallTextLight">5Y CAGR</div>
                    <div className="header2dark" style={{color: cssVar.getPropertyValue("--secondaryGreen") }}>{Number(slide.XIRR).toFixed(2) + " %"}</div>
                </div>
            </div>
            <div className="smallTextLight lineClamp">
                {
                    slide.type === 1 || slide.type === 2 ?
                            slide.Category
                            :
                            slide.type === 3 ?
                                slide.About
                                :
                                ''
                }
            </div>
        </div>)
    }
    const fund_card = (slide, fundType, i) => {
        return (<div
            key={i}
            onClick={() => {
                if (fundType === 1) {
                    navigation.push("/ETF-detail/" + slide.Symbol)
                } else if (fundType === 2) {
                    navigation.push("/fund-detail/" + slide.FundCode)
                } else if (fundType === 3) {
                    navigation.push("/mintbox-detail/" + slide.MintboxId)
                }
            }}
            style={{
                marginRight: "4%",
                padding: "4%",
                border: "1px solid " + cssVar.getPropertyValue("--light"),
                borderRadius: '8px',
                backgroundColor: cssVar.getPropertyValue("--white"),
                display: "inline-flex",
                flexDirection: "column",
                width: "80%",
                minHeight: "160px",
                whiteSpace: "normal",
            }}
        >

            <div style={{display:"flex",justifyContent:'space-between',marginBottom:"4%"}}>
                {
                    fundType === 3?
                    <MintboxIcon id={slide.MintboxId} height='40px' width='40px'/>
                    :
                    <AMCIcon amcName={slide.AMCName} height='40px' width='40px' />
                }
                <div>
                    <div className="smallTextLight" style={{border:"1px solid "+cssVar.getPropertyValue('--light'),padding:"4px 8px",borderRadius:"4px",whiteSpace:"nowrap"}}>
                        <img src={baseImgURL+'coin.webp'} style={{width:"12px",height:"12px",marginRight:"6px"}} />Min investment {CurrencyFormat(slide.MinSIPAmount)}
                    </div>
                </div>
            </div>
            <div style={{ display: "flex",marginBottom:"3%",gap:'4%' }}>
                <div className="header2dark lineClamp" style={{ flex: '1'}}>
                    {fundType === 1 ? slide.ETFName : fundType === 2 ? slide.FundName : fundType === 3 ? slide.MintboxName : ""}
                </div>
                <div>
                    <div className="smallTextLight">5Y CAGR</div>
                    <div className="header2dark" style={{color: cssVar.getPropertyValue("--secondaryGreen") }}>{Number(slide.XIRR).toFixed(2) + " %"}</div>
                </div>
            </div>
            <div className="smallTextLight lineClamp">
                {
                    fundType === 1 || fundType === 2 ?
                            slide.Category
                            :
                            fundType === 3 ?
                                slide.About
                                :
                                ''
                }
            </div>
        </div>)
    }
    const small_box = (slide) => (

        <div
            key={slide.UID}
            style={{
                borderRadius: 6, border: "1px solid " + cssVar.getPropertyValue("--light"),
                backgroundColor: slide.bgColor, padding: 16, width: 120, minHeight: 128,
                marginRight: "4%", display: "inline-flex", flexDirection: "column", whiteSpace: "normal"
            }}
            onClick={() => slide.onClick()}
        >
            <div style={{ fontSize: 14, flex: "1" }}>{slide.heading}</div>
            <AiFillRightCircle
                size={20}
                style={{
                    color: slide.btnColor,
                    alignSelf: "flex-start",
                }}
            />
        </div>

    )
    if (content.length === 0) {
        return <></>
    }
    else return (
        <div style={{ margin: "4% 0" }}>
            {header ?
                <div style={{ margin: "4% 4% 0 4%", display: 'flex', alignItems: 'center', gap:"4%" }}>
                    <div className='header1dark' style={{ flex: 1 }}>{header}</div>
                    <div style={{ margin: 'auto 0 auto 10px' }} onClick={() => rightAction()}>{renderRightSide()}</div>
                </div> 
                : ""}
            {description && type !== "videoView" ?
                <div className='bodyTextLight' style={{ margin: "1% 4% 0 4%"}} >
                    {/* <ReadMoreText text={description} /> */}
                    {description}
                </div>
                : ""}
            <div className="hiddden-scrollbar" style={{ paddingLeft: "4%",marginTop:'4%' }}>
                {type === "boxCard" && content.map(slide => (
                    box_card(slide)
                ))}
                {type === "recentlyViewed" && content.map(slide => (
                    recently_viewed(slide)
                ))}
                {type === "smallBox" && content.map(slide => (
                    small_box(slide)
                ))}
                {type === "fundCard" && content.map((slide, i) => (
                    fund_card(slide, fundType, i)
                ))}
            </div>
        </div>
    )
}

