import React from 'react'
import db from "libs/db";
import SliderComponent2 from './SliderComponent2';

export default function DisplayRecentlyViewed({navigation,header,type,currentFundId = ''}) {

    const [funds,setFunds] = React.useState([])
    
    React.useEffect(()=>{
        const keyName = type === 1 ? 'ETFs' : type === 2 ? 'MutualFunds' : type === 3 ? 'Mintboxes' : ''
        db.get(db.model.recentlyViewed).then(p => {
            if (p === null) {
                setFunds([])
            } else {
                setFunds(p[keyName].filter(f=>f.ID !== currentFundId)) // remove current fund
            } 
        })
    },[])
        
    const recentlyViewed = {
        'header': header,
        "description": "",
        "content": funds
    }
    if(funds.length === 0){
        return null
    }
    return <SliderComponent2 navigation={navigation} type="recentlyViewed" component={recentlyViewed}/>
}