import React from "react";
import { toast } from "react-toastify";
import cssVar from "views/mobileView/styles";
import db from "libs/db";
import AdminServices from "services/api/AdminServices";
import MainHeader from "views/mobileView/components/molecules/Headers/MainHeader";
import SliderComponent from "views/mobileView/components/atoms/SliderComponent";
import "bootstrap/dist/css/bootstrap.min.css";
import { BsBookmark, BsBookmarkFill } from 'react-icons/bs';
import { AiOutlineRight } from "react-icons/ai";
import HomePortfolioCard from "views/mobileView/components/molecules/Cards/HomePortfolioCard";
import Spinner from "react-bootstrap/Spinner";
import DisplayRecentlyViewed from 'views/mobileView/components/atoms/DisplayRecentlyViewed';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { initializeSmallCase, updateSmallCaseAuthId } from "services/smallcase";
import SliderComponent2 from "views/mobileView/components/atoms/SliderComponent2";
import { RiskTextColor, RiskFormat } from "views/mobileView/components/atoms/RiskFunctions";
import PauseMFModal from "views/mobileView/components/molecules/Cards/PausedMFModal";
import { baseImgURL } from "assets/Constant";

export default function DashboardScreen(props) {
  const [dashboardContent, setDashboardContent] = React.useState([]);
  const [allMintboxes, setAllMintboxes] = React.useState([])

  const [portfolio, setPortfolio] = React.useState({});
  const [portfolioPerformance, setPortfolioPerformance] = React.useState({});
  const [assetAllocation, setAssetAllocation] = React.useState([]);
  const [portfolioXIRR, setPortfolioXIRR] = React.useState({});

  const [user, setUser] = React.useState({});

  const [watchlistExists, setWatchlistExists] = React.useState(false);
  const [profiled, setProfiled] = React.useState(false);
  const [isDematLinked, setIsDematLinked] = React.useState(false);
  const [riskScore, setRiskScore] = React.useState(0);

  const [isInvested, setIsInvested] = React.useState(false)
  const [loadingDashboard, setLoadingDashboard] = React.useState(true);
  const [loadingPortfolio, setLoadingPortfolio] = React.useState(true);

  const [showPausedMfModal, setShowPausedMFModal] = React.useState(false)
  React.useEffect(async () => {
    await loadPortfolioData() //portfolio data
    await loadDashboardData()
  }, []);

  const loadDashboardData = async () => {
    setLoadingDashboard(true)
    try {
      db.get(db.model.user).then(async (p) => {
        setUser(p.userData);
        await getDashboardData();
        await getWatchlist(p.userData.id);
        await getClientData(p.userData.id)
        await getAllMintboxes(p.userData.id)
        setLoadingDashboard(false)
      })
    } catch (error) {
      toast.error(error);
      setLoadingDashboard(false)
    }
  };

  const getClientData = async (id) => {
    await db.get(db.model.clientData).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getClient(id).then(res => res);
        if (res.type === true) {
          const a = JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get Client API :", aa)
          setProfiled(aa?.Client?.[0]?.IsProfileComplete === 1)
          setIsDematLinked(aa?.SmallcaseInfo?.length !== 0)
          setRiskScore(aa?.RiskInfo?.[0]?.RiskScore || 0)
          await db.set(db.model.clientData, JSON.stringify(aa))
        }
      } else {
        setIsDematLinked(p.SmallcaseInfo?.length !== 0)
        setProfiled(p.Client?.[0]?.IsProfileComplete === 1)
        setRiskScore(p.RiskInfo?.[0]?.RiskScore || 0)
      }
    })
  };


  const getDashboardData = async () => {
    await db.get(db.model.dashboard).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getDashboardData().then(res => res);
        if (res.type === true) {
          console.log("Get Dashboard API: ", res.data)
          await db.set(db.model.dashboard, JSON.stringify(res.data))
          setDashboardContent(res.data)
        }
      } else {
        setDashboardContent(p)
      }
    })
  }

  const getWatchlist = async (id) => {
    await db.get(db.model.watchlist).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getWatchlist(id).then(res => res);
        if (res.type === true) {
          console.log("get watchlist api ", res.data)
          await db.set(db.model.watchlist, JSON.stringify(res.data))
          setWatchlistExists(res.data.length > 0)
        }
      } else {
        setWatchlistExists(p.length > 0)
      }
    })
  }

  const getAllMintboxes = async (id) => {
    await db.get(db.model.allMintboxes).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getAllMintboxes(id).then(res => res);
        if (res.type === true) {
          console.log("Get All Mintboxes API: ", res.data)
          await db.set(db.model.allMintboxes, JSON.stringify(res.data))
          setAllMintboxes(res.data)
        }
      } else {
        setAllMintboxes(p)
      }
    })
  }

  const loadPortfolioData = async () => {
    setLoadingPortfolio(true)
    try {
      db.get(db.model.user).then(async (p) => {
        setUser(p.userData);
        await getPortfolio(p.userData.id)
        await getPortfolioXIRR(p.userData.id)
        await getPortfolioPerformance(p.userData.id)
        await getPortfolioAssetAllocation(p.userData.id)
        setLoadingPortfolio(false)
      })
    } catch (error) {
      toast.error(error);
      setLoadingPortfolio(false)
    }
  };

  const getPortfolio = async (id) => {
    await db.get(db.model.portfolio).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolio(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio API :", aa)
          setPortfolio(aa)
          // setIsInvested(aa.MintboxConsolidated.length + aa.ETFConsolidated.length + aa.FundsConsolidated.length !== 0)
          setIsInvested(aa.PortfolioConsolidated && Number(aa.PortfolioConsolidated[0].PortfolioBalance) !== 0)
          await db.set(db.model.portfolio, JSON.stringify(aa))
        }
      } else {
        setPortfolio(p)
        // setIsInvested(p.MintboxConsolidated.length + p.ETFConsolidated.length + p.FundsConsolidated.length !== 0)
        setIsInvested(p.PortfolioConsolidated && Number(p.PortfolioConsolidated[0].PortfolioBalance) !== 0)
      }
    })
  }

  const getPortfolioXIRR = async (id) => {
    await db.get(db.model.portfolioXirr).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioXIRR(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          const aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolio XIRR API :", aa)
          setPortfolioXIRR(aa)
          await db.set(db.model.portfolioXirr, JSON.stringify(aa))
        }
      } else {
        setPortfolioXIRR(p)
      }
    })
  }

  const getPortfolioPerformance = async (id) => {
    await db.get(db.model.portfolioPerformance).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioPerformance(id).then(res => res);
        if (res.type === true) {
          const a = await JSON.parse(res.data);
          let aa = a.reduce((aa, f) => { return { ...aa, ...f } }, {})
          console.log("Get portfolioPerformance API :", aa)
          setPortfolioPerformance(aa)
          await db.set(db.model.portfolioPerformance, JSON.stringify(aa))
        }
      } else {
        setPortfolioPerformance(p)
      }
    })
  }

  const getPortfolioAssetAllocation = async (id) => {
    await db.get(db.model.assetAllocation).then(async (p) => {
      if (p === null) {
        let res = await AdminServices.getPortfolioAssetAllocation(id).then(res => res);
        if (res.type === true) {
          const aa = res.data;
          console.log("Get portfolio asset allocation API :", aa)
          formatAssetAllo(aa)
          await db.set(db.model.assetAllocation, JSON.stringify(aa))
        }
      } else {
        formatAssetAllo(p)
      }
    })
  }
  const formatAssetAllo = (aa) => {
    const assetClasses = [
      {
        name: 'Indian Equity',
        color: cssVar.getPropertyValue('--indianEquity')
      },
      {
        name: 'Gold',
        color: cssVar.getPropertyValue('--gold')
      },
      {
        name: 'US Equity',
        color: cssVar.getPropertyValue('--internationalEquity')
      },
      {
        name: 'Debt',
        color: cssVar.getPropertyValue('--debt')
      },
      {
        name: 'Cash',
        color: cssVar.getPropertyValue('--cash')
      },
      {
        name: 'Silver',
        color: cssVar.getPropertyValue('--silver')
      }
    ];
    let percentArr = []
    const totalAllocation = aa.reduce((total, r) => total + r.AssetBalance, 0)
    if (aa.length !== 0) {
      assetClasses.forEach(a => {
        const subTotal = aa.filter(f => f.AssetClass === a.name).reduce((total, r) => total + r.AssetBalance, 0) * 100 / totalAllocation
        if (subTotal) {
          percentArr.push({ ...a, y: Number(subTotal.toFixed(2)) })
        }
      })
    }
    setAssetAllocation(percentArr)
  }

  const onPortfolioRefresh = async () => {
    await db.remove(db.model.portfolio);
    await db.remove(db.model.portfolioXirr);
    await db.remove(db.model.portfolioPerformance);
    await db.remove(db.model.assetAllocation);
    loadPortfolioData();
  }

  const NotInvestedSlider = () => {

    const slides = [
      {
        UID: 'a',
        SubTitle: 'Invest using your demat account',
        Description: 'Smart portfolios await. Securely link your demat account to start investing!',
        ActionText: 'Link demat account',
        onClick: async () => {
          const res = await AdminServices.getSmallcaseTransactionId({ "intent": "CONNECT" }).then(rs => rs)
          if (res.type) {
            console.log("Trx id =>>", res.data.data.transactionId)
            if (window.sc === undefined) {
              console.log("Initializing Smallcase")
              await initializeSmallCase();
            }
            await window.sc.triggerTransaction({ transactionId: res.data.data.transactionId })
              .then((a) => {
                console.log(a)
                if (a) {
                  updateSmallCaseAuthId(user.id, a.smallcaseAuthToken, a.broker).then(res => {
                    console.log(res)
                    if (res) {
                      db.remove(db.model.clientData)
                      toast.success('Demat linked')
                      loadDashboardData()
                    } else {
                      toast.error('Demat already linked with another mintbox account')
                    }
                  })
                }
              })
              .catch((p) => {
                if (p.message === 'user_cancelled') {
                  return
                } else {
                  toast.error('Something went wrong! Try again.')
                }
              });
          }
        },
        Theme: 'DARKGREEN',
        URL: baseImgURL+'link_demat.webp',
        show: !isDematLinked
      },
      {
        UID: 'b',
        SubTitle: 'Don\'t have a demat? Open one now!',
        Description: 'Enter the world of stock markets by openeing your first demat account',
        ActionText: 'Open demat account',
        onClick: async () => {
          if (window.sc === undefined) {
            console.log("Initializing Smallcase")
            await initializeSmallCase();
          }
          window.sc.signup()
            .then(
              () => {
                console.log("signup flow has now ended");
              }
            );
        },
        Theme: 'GREEN',
        URL: baseImgURL+'open_demat.webp',
        show: !isDematLinked
      },
      {
        UID: 'c',
        SubTitle: 'Invest in your favourite mutual funds',
        Description: 'Commission free direct mutual funds. No demat account required!',
        ActionText: 'Activate mutual funds',
        // onClick: () => { props.history.push("/activate-direct-mf/get-started") },
        onClick: () => setShowPausedMFModal(true),
        Theme: 'GREEN',
        URL: baseImgURL+'activate_mutual_funds.webp',
        show: !profiled && !isDematLinked
      },
      // {
      //   UID: 'd',
      //   SubTitle: 'Let\'s find you some mintboxes!',
      //   Description: 'Answer a few questions and we will recommend you a few mintboxes',
      //   ActionText: <>Get started <AiOutlineRight size={12} style={{ marginLeft: '4px' }} /></>,
      //   onClick: () => { props.history.push("/risk-profile") },
      //   Theme: 'DARKGREEN',
      //   URL: baseImgURL+'perfect_mintbox.webp',
      //   show: isDematLinked
      // }
    ]
    return <SliderComponent2 navigation={props.history} type="boxCard" component={{ 'content': slides.filter(f => f.show) }} />
  }
  const Watchlist = () => {
    return (
      <div style={{
        margin: "4%",
        padding: "3%",
        border: "1px solid " + cssVar.getPropertyValue("--light"),
        borderRadius: '6px',
        display: "flex",
        justifyContent: "space-between",
        alignItems: "center",
        // backgroundColor: watchlistExists ? cssVar.getPropertyValue('--conservativeRisk') : cssVar.getPropertyValue('--lightGreenBackground')
        backgroundColor: cssVar.getPropertyValue('--lightGreenBackground')
      }}
        onClick={() => props.history.push("/watchlist")}>
        {watchlistExists ?
          <div className="bodyTextDark" style={{ color: cssVar.getPropertyValue('--darkGreen') }}>
            <BsBookmarkFill size={20} style={{marginRight:'4px'}}/> Your mintbox, ETF and MF watchlists
          </div>
          :
          <div className="bodyTextDark" style={{ color: cssVar.getPropertyValue('--darkGreen') }}>
            <BsBookmark size={20} style={{marginRight:'4px'}}/> Create a watchlist for easy tracking
          </div>
        }
        <AiOutlineRight size={18} style={{ color: cssVar.getPropertyValue('--darkGreen') }} />
      </div>
    )
  }

  const displaySection = (section) => {
    switch (section.sectionType) {
      case 'header_sec': {
        if (isInvested) {
          return <>
            <div style={{ display: "flex", alignItems: 'center', margin: "4% 4% 0" }}>
              <div className='header1dark' style={{ flex: '1' }}>
                Portfolio summary
                {loadingPortfolio ?
                  <Spinner
                    as="span"
                    animation="border"
                    size="sm"
                    role="status"
                    aria-hidden="true"
                    style={{ marginLeft: "10px" }}
                  />
                  :
                  ''
                }
              </div>
              <span onClick={() => props.history.push("/portfolio")} className='greenChipHeader'>
                Detailed view <AiOutlineRight size={8} />
              </span>
            </div>
            <HomePortfolioCard
              balance={portfolio.PortfolioConsolidated[0].PortfolioBalance}
              previousBalance={portfolio.PrevPortfolioConsolidated[0].PrevPortfolioBalance}
              xirr={portfolioXIRR.PortfolioXIRR?.XIRR}
              onRefresh={() => onPortfolioRefresh()}
              growthChartData={portfolioPerformance?.Portfolio?.map(d => [new Date(d["NavDateStr"]).getTime(), d["Balance"]])}
              pieChartData={assetAllocation}
            />
          </>
        }
        else {
          return <NotInvestedSlider />
        }
      }
      case 'video_slider': {
        return <SliderComponent navigation={props.history} type="videoView" section={section} />
      }
      case 'recently_view': {
        return <DisplayRecentlyViewed navigation={props.history} type={3} header="Recently viewed mintboxes" />
      }
      case 'action_slider': {
        return <SliderComponent navigation={props.history} type="boxCard" section={section} />
      }
      case 'watchlist': {
        return <Watchlist />
      }
      case 'reason_slider': {
        return <SliderComponent navigation={props.history} type="reasonSlider" section={section} />
      }
      case 'blog_slider': {
        return <SliderComponent navigation={props.history} type="blogView" section={section} />
      }
      case 'recommended': {
        if (riskScore !== 0) {
          const riskMintboxes = {
            'header': "mintboxes recommended for you",
            "description": <span>Because your risk profile is '<span style={{ color: RiskTextColor(riskScore), fontWeight: '500' }}>{RiskFormat(riskScore)}</span>'</span>,
            "content": allMintboxes.filter(m => m.RiskLevel === riskScore),
          }
          console.log(riskMintboxes)
          return <SliderComponent2 navigation={props.history} type="fundCard" fundType={3} component={riskMintboxes} />
        }
        else return <SliderComponent navigation={props.history} type="boxCard" section={section} />
      }
      default: {
        return <></>
      }
    }
  }


  return (
    <div className="mainContainer">
      <MainHeader
        header={<span>Hey {user.name} <AiOutlineRight size={12} className='secondaryGreen' style={{verticalAlign:'unset'}}/></span>}
        screenType="Home"
        onHeaderClick={() => { props.history.push('/menu/profile') }}
        navigation={props.history}
      />
      {
        (loadingDashboard || loadingPortfolio) ?
          <ActivityLoaderMintbox />
          :
          <div className="dataContainer px-0">
            {
              isInvested ?
                dashboardContent.filter(c => c.stageName === 'INVESTED')[0].dashboardSection.map(section => {
                  return displaySection(section)
                })
                :
                isDematLinked ?
                  dashboardContent.filter(c => c.stageName === 'LINKED_DEMAT')[0].dashboardSection.map(section => {
                    return displaySection(section)
                  })
                  :
                  dashboardContent.filter(c => c.stageName === 'NEW_USER')[0].dashboardSection.map(section => {
                    return displaySection(section)
                  })
            }
          </div>
      }
      <PauseMFModal navigation={props.history} showModal={showPausedMfModal} onClose={() => setShowPausedMFModal(false)} />
    </div>
  )
}

